var website_name = require('App/Constants/app-config').website_name;

var Client = require('./client_base');

var getElementById = require('../common_functions').getElementById;

var getLanguage = require('../language').get;

var isMobile = require('../os_detect').isMobile;

var isStorageSupported = require('../storage').isStorageSupported;

var LocalStore = require('../storage').LocalStore;

var urlForCurrentDomain = require('../url').urlForCurrentDomain;

var domain_app_ids = require('../../config').domain_app_ids;

var getAppId = require('../../config').getAppId;

var Login = function () {
  var redirectToLogin = function redirectToLogin() {
    if (!Client.isLoggedIn() && !isLoginPages() && isStorageSupported(sessionStorage)) {
      sessionStorage.setItem('redirect_url', window.location.href);
      window.location.href = loginUrl();
    }
  };

  var loginUrl = function loginUrl() {
    var server_url = localStorage.getItem('config.server_url');
    var language = getLanguage();
    var signup_device = LocalStore.get('signup_device') || (isMobile() ? 'mobile' : 'desktop');
    var date_first_contact = LocalStore.get('date_first_contact');
    var marketing_queries = "&signup_device=".concat(signup_device).concat(date_first_contact ? "&date_first_contact=".concat(date_first_contact) : '');
    var default_login_url = "https://oauth.deriv.app/oauth2/authorize?app_id=".concat(getAppId(), "&l=").concat(language).concat(marketing_queries, "&brand=").concat(website_name.toLowerCase());

    if (server_url && /qa/.test(server_url)) {
      return "https://".concat(server_url, "/oauth2/authorize?app_id=").concat(getAppId(), "&l=").concat(language).concat(marketing_queries);
    }

    if (getAppId === domain_app_ids['deriv.app']) {
      return default_login_url;
    }

    return urlForCurrentDomain(default_login_url);
  }; // TODO: update this to handle logging into /app/ url


  var isLoginPages = function isLoginPages() {
    return /logged_inws|redirect/i.test(window.location.pathname);
  };

  var socialLoginUrl = function socialLoginUrl(brand) {
    return "".concat(loginUrl(), "&social_signup=").concat(brand);
  };

  var initOneAll = function initOneAll() {
    ['google', 'facebook'].forEach(function (provider) {
      var el_button = getElementById("#button_".concat(provider));
      el_button.removeEventListener('click');
      el_button.addEventListener('click', function (e) {
        e.preventDefault();
        window.location.href = socialLoginUrl(provider);
      });
    });
  };

  return {
    redirectToLogin: redirectToLogin,
    isLoginPages: isLoginPages,
    initOneAll: initOneAll
  };
}();

module.exports = Login;