import extend from 'extend';
import { isDigitContract } from 'Stores/Modules/Contract/Helpers/digits';
import { isUserSold, getEndTime } from 'Stores/Modules/Contract/Helpers/logic';
import { MARKER_TYPES_CONFIG } from '../../SmartChart/Constants/markers';

var createMarkerConfig = function createMarkerConfig(marker_type, x, y, content_config) {
  return extend(true, {}, MARKER_TYPES_CONFIG[marker_type], {
    marker_config: {
      x: +x,
      y: y
    },
    content_config: content_config
  });
};

export var getSpotCount = function getSpotCount(contract_info, spot_count) {
  return isDigitContract(contract_info.contract_type) ? spot_count + 1 : spot_count;
}; // -------------------- Lines --------------------

export var createMarkerEndTime = function createMarkerEndTime(contract_info) {
  var end_time = getEndTime(contract_info);
  if (!end_time) return false;
  return createMarkerConfig(MARKER_TYPES_CONFIG.LINE_END.type, +end_time, null, {
    status: "".concat(contract_info.profit > 0 ? 'won' : 'lost'),
    marker_config: MARKER_TYPES_CONFIG
  });
};
export var createMarkerPurchaseTime = function createMarkerPurchaseTime(contract_info) {
  if (!contract_info.purchase_time || !contract_info.date_start || +contract_info.purchase_time === +contract_info.date_start) return false;
  return createMarkerConfig(MARKER_TYPES_CONFIG.LINE_PURCHASE.type, +contract_info.purchase_time);
};
export var createMarkerStartTime = function createMarkerStartTime(contract_info) {
  if (!contract_info.date_start) return false;
  return createMarkerConfig(MARKER_TYPES_CONFIG.LINE_START.type, +contract_info.date_start, null, {
    marker_config: MARKER_TYPES_CONFIG
  });
}; // -------------------- Spots --------------------

export var createMarkerSpotEntry = function createMarkerSpotEntry(contract_info) {
  if (!contract_info.entry_tick_time) return false;
  var marker_type = MARKER_TYPES_CONFIG.SPOT_ENTRY.type;
  var component_props = {};
  var entry_tick = contract_info.entry_tick_display_value;
  var spot_has_label = isDigitContract(contract_info.contract_type);

  if (spot_has_label) {
    marker_type = MARKER_TYPES_CONFIG.SPOT_MIDDLE.type;
    component_props = {
      spot_value: "".concat(entry_tick),
      spot_epoch: "".concat(contract_info.entry_tick_time),
      spot_count: 1
    };
  }

  return createMarkerConfig(marker_type, contract_info.entry_tick_time, entry_tick, component_props);
};
export var createMarkerSpotExit = function createMarkerSpotExit(contract_info, tick, idx) {
  if (!contract_info.exit_tick_time) return false;
  var is_user_sold = isUserSold(contract_info);
  var spot_count, align_label;

  if (tick) {
    spot_count = getSpotCount(contract_info, idx);
    align_label = tick.align_label;
  }

  var exit_tick = contract_info.exit_tick_display_value;
  return createMarkerConfig(!is_user_sold ? MARKER_TYPES_CONFIG.SPOT_EXIT.type : MARKER_TYPES_CONFIG.SPOT_SELL.type, +contract_info.exit_tick_time, +exit_tick, !is_user_sold ? {
    spot_value: "".concat(exit_tick),
    spot_epoch: "".concat(contract_info.exit_tick_time),
    status: "".concat(+contract_info.profit > 0 ? 'won' : 'lost'),
    align_label: align_label,
    spot_count: spot_count
  } : {});
};
export var createMarkerSpotMiddle = function createMarkerSpotMiddle(contract_info, tick, idx) {
  var spot_count = getSpotCount(contract_info, idx);
  var spot = tick.tick_display_value;
  var marker_config = createMarkerConfig(MARKER_TYPES_CONFIG.SPOT_MIDDLE.type, +tick.epoch, +spot, {
    spot_value: "".concat(spot),
    spot_epoch: "".concat(tick.epoch),
    align_label: tick.align_label,
    spot_count: spot_count
  });
  marker_config.type = "".concat(marker_config.type, "_").concat(idx);
  return marker_config;
};