import { action } from 'mobx';
import { localize } from 'App/i18n';
var common_store;

var OutdatedBrowser = function () {
  var init = function init(store) {
    common_store = store.common;
    var src = '//browser-update.org/update.min.js';
    if (document.querySelector("script[src*=\"".concat(src, "\"]"))) return;
    window.$buoop = {
      vs: {
        i: 11,
        f: -4,
        o: -4,
        s: 9,
        c: -4
      },
      api: 4,
      url: 'https://whatbrowser.org/',
      noclose: true,
      // Do not show the 'ignore' button to close the notification
      reminder: 0,
      // show all the time
      onshow: updateStore,
      nomessage: true,
      insecure: true
    };

    if (document.body) {
      var script = document.createElement('script');
      script.setAttribute('src', src);
      document.body.appendChild(script);
    }
  };

  var updateStore = action('showError', function () {
    if (common_store) {
      // TODO: i18n_issue
      common_store.showError({
        message: localize('Your web browser is out of date and may affect your trading experience. Please {{opening_tag}}update your browser{{closing_tag}}.', {
          opening_tag: '<a href="http://outdatedbrowser.com" rel="noopener noreferrer" target="_blank" class="link">',
          closing_tag: '</a>',
          interpolation: {
            escapeValue: false
          }
        }),
        has_html: true
      });
    }
  });
  return {
    init: init
  };
}();

export default OutdatedBrowser;