import { lazy } from 'react';
import { Redirect as RouterRedirect } from 'react-router-dom';
import { Redirect } from 'App/Containers/Redirect';
import { localize } from 'App/i18n';
import { routes } from 'Constants';
import Trade from 'Modules/Trading';
var ContractDetails = lazy(function () {
  return import(
  /* webpackChunkName: "contract" */
  'Modules/Contract');
}); // Reports Routes

var Reports = lazy(function () {
  return import(
  /* webpackChunkName: "reports" */
  'Modules/Reports');
});
var OpenPositions = lazy(function () {
  return import(
  /* webpackChunkName: "open_positions" */
  'Modules/Reports/Containers/open-positions.jsx');
});
var ProfitTable = lazy(function () {
  return import(
  /* webpackChunkName: "profit_table" */
  'Modules/Reports/Containers/profit-table.jsx');
});
var Statement = lazy(function () {
  return import(
  /* webpackChunkName: "statement" */
  'Modules/Reports/Containers/statement.jsx');
}); // Error Routes

var Page404 = lazy(function () {
  return import(
  /* webpackChunkName: "404" */
  'Modules/Page404');
});

var initRoutesConfig = function initRoutesConfig() {
  return [{
    path: routes.contract,
    component: ContractDetails,
    title: localize('Contract Details'),
    is_authenticated: true
  }, {
    path: routes.index,
    component: RouterRedirect,
    title: '',
    to: routes.trade
  }, {
    path: routes.reports,
    component: Reports,
    is_authenticated: true,
    title: localize('Reports'),
    routes: [{
      path: routes.positions,
      component: OpenPositions,
      title: localize('Open Positions'),
      icon_component: 'IconOpenPositions',
      "default": true
    }, {
      path: routes.profit,
      component: ProfitTable,
      title: localize('Profit Table'),
      icon_component: 'IconProfitTable'
    }, {
      path: routes.statement,
      component: Statement,
      title: localize('Statement'),
      icon_component: 'IconStatement'
    }]
  }, {
    path: routes.trade,
    component: Trade,
    title: localize('Trade'),
    exact: true
  }, {
    path: routes.error404,
    component: Page404,
    title: localize('Error 404')
  }, {
    path: routes.redirect,
    component: Redirect,
    title: localize('Redirect')
  }];
};

var routesConfig; // For default page route if page/path is not found, must be kept at the end of routes_config array

var route_default = {
  component: Page404,
  title: localize('Error 404')
};

var getRoutesConfig = function getRoutesConfig() {
  if (!routesConfig) {
    routesConfig = initRoutesConfig();
  }

  routesConfig.push(route_default);
  return routesConfig;
};

export default getRoutesConfig;