import moment from 'moment';
import { localize } from 'App/i18n';
export var addCommaToNumber = function addCommaToNumber(num, decimal_places) {
  if (!num || isNaN(num)) {
    return num;
  }

  var n = String(decimal_places ? (+num).toFixed(decimal_places) : num);
  var p = n.indexOf('.');
  return n.replace(/\d(?=(?:\d{3})+(?:\.|$))/g, function (m, i) {
    return p <= 0 || i < p ? "".concat(m, ",") : m;
  });
};
export var getTimePercentage = function getTimePercentage(server_time, start_time, expiry_time) {
  var duration_from_purchase = moment.duration(moment.unix(expiry_time).diff(moment.unix(start_time)));
  var duration_from_now = moment.duration(moment.unix(expiry_time).diff(server_time));
  var percentage = duration_from_now.asMilliseconds() / duration_from_purchase.asMilliseconds() * 100;

  if (percentage < 0.5) {
    percentage = 0;
  } else if (percentage > 100) {
    percentage = 100;
  }

  return Math.round(percentage);
};
export var getBarrierLabel = function getBarrierLabel(contract_info) {
  if (isDigitType(contract_info.contract_type)) {
    return localize('Target');
  }

  return localize('Barrier');
};
export var getBarrierValue = function getBarrierValue(contract_info) {
  if (isDigitType(contract_info.contract_type)) {
    return digitTypeMap(contract_info)[contract_info.contract_type];
  }

  return addCommaToNumber(contract_info.barrier);
};
export var isDigitType = function isDigitType(contract_type) {
  return /digit/.test(contract_type.toLowerCase());
};

var digitTypeMap = function digitTypeMap(contract_info) {
  return {
    DIGITDIFF: localize('Not {{barrier}}', {
      barrier: contract_info.barrier
    }),
    DIGITEVEN: localize('Even'),
    DIGITMATCH: localize('Equals {{barrier}}', {
      barrier: contract_info.barrier
    }),
    DIGITODD: localize('Odd'),
    DIGITOVER: localize('Over {{barrier}}', {
      barrier: contract_info.barrier
    }),
    DIGITUNDER: localize('Under {{barrier}}', {
      barrier: contract_info.barrier
    })
  };
};