import { toMoment } from 'Utils/Date';
export var getDecade = function getDecade(moment_date) {
  return "".concat(toMoment(moment_date).year(), "-").concat(toMoment(moment_date).add(9, 'years').year());
};
export var getCentury = function getCentury(moment_date) {
  return "".concat(toMoment(moment_date).year(), "-").concat(toMoment(moment_date).add(99, 'years').year());
};